<template>
  <div>
    <br>
    <h3>付刚的项目</h3>
    <br>
    <div class="card border-secondary" @click="link('/projects/1')">
      <div class="card-body">
        <h4 class="card-title">大鹏新区生态环境动态监测系统</h4>
        <div class="card-subtitle mb-2 text-muted">2018年8月6日</div>
        <p class="card-text">深圳市大鹏新区对于自然资源、裸露地块、生态指数、水务信息、高空视频、尾气监测和近海水域水质的智慧监测系统。</p>
        <img style="width: 100%; display: block;" src="@/assets/image/project1-0.png"/>
      </div>
    </div>
    <br>
    <div class="card border-secondary" @click="link('/projects/2')">
      <div class="card-body">
        <h4 class="card-title">新华区公共服务可视物联网平台</h4>
        <div class="card-subtitle mb-2 text-muted">2018年5月21日</div>
        <p class="card-text">石家庄市新华区对于自然环境、安全生产、药食安全、电梯保障、教育安全等公共服务的大数据信息化可视平台。</p>
        <img style="width: 100%; display: block;" src="@/assets/image/project2-0.png"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods:{
    link:function(to){
      this.$router.push(to)
    }
  }
}
</script>



